import { isWindows } from '@/utils/os'
import resizeImage from '@/utils/resizeImage'

const EMPTY_IMAGE =
  'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII='

export default async function createPageScreenshot(url) {
  const base64 = await captureInBackground(url)

  if (base64 === EMPTY_IMAGE) {
    return EMPTY_IMAGE
  }

  const image = await resizeImage(base64)

  return image
}

const captureInBackground = (url) => {
  return new Promise((resolve, reject) => {
    browser.windows
      .create({
        url: url,
        focused: !1,
        width: isWindows() ? 100 : 1,
        height: isWindows() ? 100 : 1,
        left: 0,
        top: 0,
        type: 'popup'
      })
      .then((a) => {
        if (!a.tabs || !a.tabs.length) {
          browser.windows.remove(a.id)
          return reject(null)
        }

        const c = a.tabs[0].id
        let d
        let loadCompleteTime = null

        browser.tabs.update(c, {
          muted: !0
        })
        browser.windows.update(a.id, {
          focused: !1,
          width: 1440,
          height: 900,
          left: 0,
          top: 0
        })

        // Increased timeout to 12 seconds to accommodate longer wait time
        const e = setTimeout(() => {
          clearInterval(d)
          browser.windows.remove(a.id)
          resolve(EMPTY_IMAGE)
        }, 12000)

        d = setInterval(() => {
          browser.tabs.get(c).then((tab) => {
            if (tab.status === 'complete') {
              // Mark when the page first became complete
              if (!loadCompleteTime) {
                loadCompleteTime = Date.now()
              }

              // Wait for 5 seconds after the page reports 'complete'
              // This gives time for images, fonts, and dynamic content to load
              const timeWaited = Date.now() - loadCompleteTime
              if (timeWaited >= 4500) {
                clearInterval(d)
                clearTimeout(e)

                browser.tabs
                  .captureVisibleTab(a.id)
                  .then((screenshot) => {
                    browser.windows.remove(a.id).then(() => {
                      screenshot ? resolve(screenshot) : resolve(EMPTY_IMAGE)
                    })
                  })
                  .catch(() => {
                    browser.windows.remove(a.id).then(() => {
                      resolve(EMPTY_IMAGE)
                    })
                  })
              }
            }
          })
        }, 250)
      })
  })
}
