/**
 * LogoService - Generates logo URLs for hostnames
 *
 * This service encodes hostnames to create URLs for fetching logos
 * from the SpeedDial image service.
 */

// XOR encryption pattern used to encode hostnames
const ENCRYPTION_PATTERN = [23, 91, 45, 67, 12, 88, 34, 76, 55, 19, 62, 41, 99, 17, 83]

/**
 * Encodes a hostname string using XOR encryption and base64 encoding
 * @param {string} hostname - The hostname to encode
 * @returns {string} - URL-safe base64 encoded string
 */
function encodeHostname(hostname) {
  const encodedChars = []

  for (let i = 0; i < hostname.length; i++) {
    const charCode = hostname.charCodeAt(i)
    const patternValue = ENCRYPTION_PATTERN[i % ENCRYPTION_PATTERN.length]
    const indexMultiplier = i * 3

    // XOR encryption: charCode ^ patternValue ^ (index * 3)
    encodedChars.push(charCode ^ patternValue ^ indexMultiplier)
  }

  // Convert array of character codes back to string
  const encodedString = String.fromCharCode(...encodedChars)

  // Base64 encode
  let base64Encoded = btoa(encodedString)

  // Make URL-safe: replace + with -, / with _, and remove padding =
  base64Encoded = base64Encoded.replace(/\+/g, '-')
  base64Encoded = base64Encoded.replace(/\//g, '_')
  base64Encoded = base64Encoded.replace(/=/g, '')

  return base64Encoded
}

// Base URL for logo service
const LOGO_SERVICE_BASE_URL = 'https' + '://' + 'image' + '.' + 'speeddial2' + '.' + 'com' + '/' + 'logo' + '/'

/**
 * Generates a logo URL for a given hostname
 * @param {string} hostname - The hostname to generate a logo URL for
 * @returns {string} - Complete URL to the logo image
 */
export function getLogoUrl(hostname) {
  const encodedHostname = encodeHostname(hostname)
  return LOGO_SERVICE_BASE_URL + encodedHostname
}

// Export the encoder function for backward compatibility
export default encodeHostname
