export function validateUrl(url) {
  var pattern = /(ftp|http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-/]))?/
  return pattern.test(url)
}

export function fixUrl(url) {
  if (!url) return url
  if (
    url.indexOf('http://') != 0 &&
    url.indexOf('https://') != 0 &&
    url.indexOf('smtp://') != 0 &&
    url.indexOf('ftp://') != 0 &&
    url.indexOf('rtp://') != 0 &&
    url.indexOf('irc://') != 0 &&
    url.indexOf('file://') != 0 &&
    url.indexOf('chrome://') != 0 &&
    url.indexOf('moz://') != 0 &&
    url.indexOf('moz-extension://') != 0 &&
    url.indexOf('snmp://') != 0 &&
    url.indexOf('chrome-extension') == -1
  ) {
    url = 'https://' + url
  }
  return url
}

export function getHostname(url) {
  if (typeof url != 'undefined' && url.indexOf('http') == 0) {
    return url.split(/\/+/g)[1]
  } else {
    return
  }
}

export function suggestTitleFromUrl(url) {
  let name = url
  name = name
    .replace('http://', '')
    .replace('https://', '')
    .replace('.org', '')
    .replace('.com', '')
    .replace('.net', '')
    .replace('www.', '')
    .replace('/', ' ')
  return name.charAt(0).toUpperCase() + name.slice(1)
}

/**
 * Finds a matching popular website by hostname
 * Normalizes hostnames by removing 'www.' prefix and checks both the main URL and alternativeUrls
 * @param {string} hostname - The hostname to match
 * @param {Array} popularWebsites - Array of popular websites to search through
 * @returns {Object|undefined} The matched popular website object or undefined if no match found
 */
export function findPopularWebsiteByHostname(hostname, popularWebsites) {
  if (!hostname) return undefined

  const normalizedHostname = hostname.replace(/^www\./, '')

  return popularWebsites.find(({ url, alternativeUrls }) => {
    const popularHostname = getHostname(url)?.replace(/^www\./, '')
    const altHostnames = alternativeUrls
      ? alternativeUrls.split(',').map((u) => getHostname(u.trim())?.replace(/^www\./, ''))
      : []
    return popularHostname === normalizedHostname || altHostnames.includes(normalizedHostname)
  })
}
